<template>
  <div class="setting bgfff">
    <h2 class="title">作业配置</h2>
    <div class="setting-content">
      <el-table
        header-row-class-name="table-header"
        row-class-name="table-tr"
        :data="tableData"
        border
        height="513"
        style="width: 100%"
      >
        <el-table-column align="center" show-overflow-tooltip prop="name" label="发起方"></el-table-column>
        <el-table-column label="操作" align="center">
          <template slot-scope="scope">
            <el-radio v-model="scope.row.authAutoType" label="autoAccept">自动同意</el-radio>
            <el-radio v-model="scope.row.authAutoType" label="autoReject">自动拒绝</el-radio>
            <el-radio v-model="scope.row.authAutoType" label="manual">手动操作</el-radio>
          </template>
        </el-table-column>
      </el-table>
      <div class="identity-pagination">
        <span>共{{total}}条记录</span>
        <span class="right-part">
          <span style="margin-right:10px">共{{totalPage}}页</span>
          <el-select
            v-model="pageSizeList.value"
            @change="handleSizeChange"
            class="page-size-select"
            placeholder="请选择"
          >
            <el-option
              v-for="item in pageSizeList.options"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
          <el-pagination
            @current-change="handleCurrentChange"
            :current-page="currentPage"
            :page-size="size"
            layout="prev, pager, next"
            :total="total"
          ></el-pagination>
        </span>
      </div>
    </div>
    <div class="setting-compute-footor">
      <el-button class="normal-button" @click="$router.go(-1)">取消</el-button>
      <el-button @click="postConfig">保存</el-button>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      initModel: {},
      tableData: [],
      currentPage: 1,
      total: 0,
      totalPage: 0,
      size: 10,
      pageSizeList: {
        value: 10,
        options: [
          {
            value: 10,
            label: '每页10条'
          },
          {
            value: 30,
            label: '每页30条'
          },
          {
            value: 50,
            label: '每页50条'
          }
        ]
      }
    }
  },
  methods: {
    handleSizeChange(val) {
      this.size = val
      this.getAuthList({
        page: this.currentPage - 1,
        size: val,
        status: 'accepted'
      })
    },
    handleCurrentChange(val) {
      this.currentPage = val
      this.getAuthList({
        page: this.currentPage - 1,
        size: this.size,
        status: 'accepted'
      })
    },
    getAuthList(param) {
      let params = param || {
        page: this.currentPage - 1,
        size: this.size,
        status: 'accepted'
      }
      this.ApiService.getAuthList(params).then(
        res => {
          let arr = []
          res.data.content.forEach(element => {
            arr.push({
              id: element.id,
              name: element.senderCompany,
              authAutoType: element.authAutoType || 'manual'
            })
          })
          this.tableData = arr
          this.total = res.data.totalElements
          this.totalPage = res.data.totalPages
        },
        err => {
          this.Utils.message('warning', err.message)
        }
      )
    },
    postConfig() {
      let params = []
      this.tableData.forEach(element => {
        params.push({
          id: element.id,
          authAutoType: element.authAutoType
        })
      })
      this.ApiService.postAuthAuto(params).then(res => {
        if (res.code === 0) {
          this.Utils.message('保存成功！', 'success')
        } else {
          this.Utils.message(res.message || '保存失败！', 'warning')
        }
      })
    }
  },
  mounted() {
    this.getAuthList()
  }
}
</script>


<style lang="less" scoped>
.setting {
  height: 100%;
  // min-height: 750px;
  width: 100%;
  position: relative;
  .title {
    height: 50px;
    font-size: 14px;
    line-height: 50px;
    font-weight: bold;
    padding-left: 20px;
    border-bottom: 1px solid #ebeef9;
  }
  .setting-content {
    // height: 600px;
    margin: 31px 20px;
    min-height: 600px;
    // display: flex;
    // flex-direction: column;
  }
  .setting-compute-footor {
    height: 50px;
    line-height: 50px;
    padding: 0px 20px;
    border-top: 1px solid #ebeef9;
    position: absolute;
    bottom: 0px;
    width: 100%;
    text-align: right;
    .el-button {
      width: 96px;
    }
  }
}
</style>
<style lang="less">
.setting {
  .table-tr {
    height: 48px;
    td {
      padding: 0px;
      font-size: 12px;
      color: #333333;
      letter-spacing: 0;
      .cell {
        font-size: 12px;
        color: #333333;
        letter-spacing: 0;
      }
    }
  }
  .table-header {
    height: 32px;
    th {
      height: 32px;
      font-size: 12px;
      color: #333333;
      letter-spacing: 0;
      font-weight: bold;
      padding: 0;
      background: #fbfcfd;
    }
  }
  .el-table th.gutter {
    display: table-cell !important;
  }
  .el-table colgroup.gutter {
    display: table-cell !important;
  }
  .el-table-column--selection .cell {
    padding-right: 10px;
  }
  .identity-pagination {
    position: absolute;
    bottom: 60px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: calc(100% - 40px);
    font-size: 12px;
    color: #333333;
    letter-spacing: 0;
    line-height: 12px;
    .right-part {
      display: flex;
      align-items: center;
      .el-pagination__total {
        float: left;
      }
      .el-pagination__sizes {
        float: left;
      }
      .el-pager {
        li {
          font-size: 12px;
        }
      }
    }
    .page-size-select {
      width: 93px;
      height: 24px;
      .el-input__inner {
        width: 93px;
        height: 24px;
        padding-right: 10px;
        font-size: 12px;
        color: #333333;
        letter-spacing: 0;
        background: #fbfcfd;
        border: 1px solid #ebeef9;
        border-radius: 2px;
        border-radius: 2px;
      }
    }
  }
}
</style>